import React, { Component } from 'react';
import Slider from "react-slick";

function numberFormat(inputNumber) {
    return inputNumber.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
}



export default class FavSlider extends Component {
    render() {
        const settings = {
            dots: false,
            infinite: true,
            speed: 500,
            slidesToShow: 4,
            slidesToScroll: 1,
            //   autoplay:true,
            //   autoplaySpeed:5000 /*1000당 1초*/ 
        };

        const multisliderimgs = [
            {
                pid: 1,
                src: "img/shoes-Image/Jordan-1-Retro-High-Travis-Scott/488879_01.jpg.jpeg",
                mainModel: "조던 1 레트로 하이",
                subModel: "트레비스 스캇",
                lowestSellPrice: 2380000, /*추후 수정 필요*/
                link: "/product",
                alt: "aj1-travis-scott"
            },
            {
                pid: 2,
                src: "img/shoes-Image/Air-Jordan-1-Retro-High-OG-'Obsidian'/482560_01.jpg.jpeg",
                mainModel: "조던 1 레트로 하이",
                subModel: "옵시디언 UNC",
                lowestSellPrice: 350000,
                link: "/",
                alt: "aj1-obsidian-unc"
            },
            {
                pid: 3,
                src: "img/shoes-Image/adidas-Yeezy-500-Blush/317455_01.jpg.jpeg",
                mainModel: "이지 500",
                subModel: "블러쉬",
                lowestSellPrice: 380000,
                link: "/",
                alt: "yeezy-500-blush"
            },
            {
                pid: 4,
                src: "img/shoes-Image/Jordan-1-Retro-High-Satin-Black-Toe-(W)/508716_01.jpg.jpeg",
                mainModel: "조던 1 레트로 하이",
                subModel: "사틴 블랙토",
                lowestSellPrice: 680000,
                link: "/",
                alt: "aj1-satin-blacktoe"
            },
            {
                pid: 5,
                src: "img/shoes-Image/Jordan-1-Retro-High-Travis-Scott/488879_01.jpg.jpeg",
                mainModel: "조던 1 레트로 하이",
                subModel: "트레비스 스캇",
                lowestSellPrice: 2380000, /*추후 수정 필요*/
                link: "product.html",
                alt: "aj1-travis-scott"
            },
            {
                pid: 6,
                src: "img/shoes-Image/Air-Jordan-1-Retro-High-OG-'Obsidian'/482560_01.jpg.jpeg",
                mainModel: "조던 1 레트로 하이",
                subModel: "옵시디언 UNC",
                lowestSellPrice: 350000,
                link: "/",
                alt: "aj1-obsidian-unc"
            },
            {
                pid: 7,
                src: "img/shoes-Image/adidas-Yeezy-500-Blush/317455_01.jpg.jpeg",
                mainModel: "이지 500",
                subModel: "블러쉬",
                lowestSellPrice: 380000,
                link: "/",
                alt: "yeezy-500-blush"
            },
            {
                pid: 8,
                src: "img/shoes-Image/Jordan-1-Retro-High-Satin-Black-Toe-(W)/508716_01.jpg.jpeg",
                mainModel: "조던 1 레트로 하이",
                subModel: "사틴 블랙토",
                lowestSellPrice: 680000,
                link: "/",
                alt: "aj1-satin-blacktoe"
            }
        ]

        var multisliderList = [];
        var i = 0;
        while (i < multisliderimgs.length) {
            multisliderList.push(
                <div className="product">
                    <div className="product-photo">
                        <a href={multisliderimgs[i].link}>
                            <img src={multisliderimgs[i].src} alt={multisliderimgs[i].alt}></img>
                        </a>
                    </div>
                    <div className="product-title">
                        <div className="main-model">{multisliderimgs[i].mainModel}</div>
                        <div className="sub-model">{multisliderimgs[i].subModel}</div>
                    </div>
                    <div className="product-price">최저 금액 : {numberFormat(multisliderimgs[i].lowestSellPrice)}원</div>
                </div>
            )
            i++;
        }
        return (
            <div className="favorite-item-group">
                <img className="group-title" src="img/icon/hambuger_icon_2_A0_Rectangle_6_pattern.png" alt="hamburger-icon" width="45px" height="45px" />
                    <div className="favorite-item-title">인기 상품<br /><br /></div>
                    <Slider {...settings}>
                            {multisliderList}
                    </Slider>
            </div>


        );
    }
}

