import React, { Component } from 'react';
import ProductComment from 'Components/ProductComment';
import Subheader from 'Components/Subheader';
import ProductDesc from '../Components/ProductDesc';
import ProductButtons from '../Components/ProductButtons';
import ProductBuy from '../Components/ProductBuy';
import ProductSell from '../Components/ProductSell';
import ProductSubinfo from '../Components/ProductSubinfo';

const productInfo =
{
    pid: 1,
    src: "img/shoes-Image/Jordan-1-Retro-High-Travis-Scott/488879_01.jpg.jpeg",
    mainModel: "조던 1 레트로 하이",
    subModel: "트레비스 스캇",
    mainModeleng: "Jordan 1 Retro High",
    subModeleng: "Travis Scott",
    productSubinfo: "발매일 : 2019년 05월 11일" + <br />
        + "발매가격 : 239,000원(175$)" + <br /> +
        "제품번호 : CD4487-100" + <br /> +
        "색상 : Sail/Black-Dark Mocha-University Red",
    productDesc: "클래식 시리즈 에어 조던 1 레트로 하이 OG '다크 모카'. 지난 10월부터 트래비스 스캇의 데일리 룩을 장식한 신발입니다." + <br /> +
        "군더더기 없이 깔끔한 실루엣이 돋보이는 제품은 브라운, 화이트와의 조화가 인상적입니다." + <br /> +
        "거꾸로 뒤집힌 스우시 디자인과 숨김형 파우치가 특별함을 더해줍니다." + <br /> +
        "이 운동화는 2019년 5월에 출시되어 175달러에 판매되었습니다." + <br />,
    lowestSellPrice: 2380000, /*추후 수정 필요*/
    highestBuyPrice: 2000000,
    link: "/product",
    alt: "aj1-travis-scott"
}


class Product extends Component {
    constructor(props) {
        super(props);
        this.state = {
            mode: 'main'
        }
    }

    render() {
        
        const left = {
            float: 'left'
          }

        // const productMode = this.state.mode;
        // let buyOrsell = null;

        // if(productMode === 'main'){
        //     buyOrsell = <ProductSubinfo
        //     subinfo={productInfo.productSubinfo}
        //     ></ProductSubinfo>
        // }
        // else if(productMode === 'buy'){
        //     buyOrsell = <ProductBuy></ProductBuy>
        // }
        // else if(productMode === 'sell'){
        //     buyOrsell = <ProductSell></ProductSell>
        // }
        

        return (
            <div>
                <Subheader />
                <div className="product-contents-area">
                    <div className="product-main-model-kor">{productInfo.mainModel} {productInfo.subModel}</div>
                        <ProductButtons
                            buyMode={function(){
                                this.setState({mode:'buy'})
                            }.bind(this)}
                            sellMode={function(){
                                this.setState({mode:'sell'})
                            }.bind(this)}
                            mainMode={function(){
                                this.setState({mode:'main'})
                            }.bind(this)}
                        >
                        </ProductButtons>
                        <div className="product-main-model-eng" style={left}>{productInfo.mainModeleng} {productInfo.subModeleng}</div>
                    <div className="product-photo-detail-area">
                        <img src={productInfo.src} alt={productInfo.alt} />
                    </div>
                    <div className="product-info">
                        {this.state.mode==='main' ?  
                        <ProductSubinfo
                            product = {productInfo}
                        ></ProductSubinfo> : 
                        this.state.mode==='buy' ? 
                        <ProductBuy></ProductBuy> : 
                        this.state.mode==='sell' ? 
                        <ProductSell></ProductSell> : 
                        <div>Not Found</div>
                        }
                    </div>

                    {/* <div>
                        {
                            function(){
                                if(this.state.mode === 'main'){
                                    return (<ProductSubinfo
                                            subinfo={productInfo.productSubinfo}
                                           ></ProductSubinfo>);
                                }
                                else if(this.state.mode === 'buy'){
                                    return (<ProductBuy></ProductBuy>);
                                }
                                else if(this.state.mode === 'sell'){
                                    return (<ProductSell></ProductSell>);
                                }
                            }()
                        }
                    </div> */}
                    <ProductDesc
                        product={productInfo}>
                    </ProductDesc>
                    <ProductComment />
                </div>

                {/* 추천 상품 들어갈 곳  */}
            </div>
        );
    }
}

export default Product;