import React, { Component } from 'react';

const left = {
    float: 'left'
}

class ProductDesc extends Component {

    render() {
        var prd = this.props.product;

        return (
            <div className="product-desc">
            <div className="product-common-title" style={left}>
              <div className="product-common-title-icon" style={left} >
                <img src="img/icon/square_icon.png" width="27px" height="27px" alt="square_icon" />
              </div>
              상품 설명</div>
            <div className="product-desc-contents">{prd.productDesc}</div>
  
          </div>
        );
    }
}

export default ProductDesc;