import React, {Component} from 'react';
import Slider from "react-slick";

 
export default class SingleSlider extends Component {
  render() {
    const settings = {
      dots: true,
      infinite: true,
      speed: 500,
      slidesToShow: 1,
      slidesToScroll: 1,
      autoplay:true,
      autoplaySpeed:5000, /*1000당 1초*/ 
      arrows:false
    };

    const singlesliderimgs = [
        { src:"img/slider/roll1.jpg", alt:"sliderimg1", link:"1"},
        { src:"img/slider/roll2.jpg", alt:"sliderimg2", link:"2"},
        { src:"img/slider/roll3.jpg", alt:"sliderimg3", link:"3"}
    ]

    var singlesliderList = [];
    var i = 0;
    while(i<singlesliderimgs.length){
        singlesliderList.push(
        <div>
          <a href={singlesliderimgs[i].link}>
          <img src={singlesliderimgs[i].src} 
               alt={singlesliderimgs[i].alt} 
               width="100%"
               height="400px"/>
          </a>
        </div>)
        i++;
    }
    return (
        <div className="slider-area">
      <Slider {...settings}>
          {singlesliderList}        
      </Slider>
      </div>
    );
  }
}

