import React, { Component } from 'react';

class ProductButtons extends Component {   
    render()  
    {
        const right = {
            float: 'right'
          }
        return (
            <div className="buttons" style={right}>
                <input type="button" value="BUY" className="buy-button"
                    onClick={function(){
                    this.props.buyMode();
                    }.bind(this)}
                    />
                <input type="button" value="SELL" className="sell-button" 
                    onClick={function(){
                    this.props.sellMode();
                    }.bind(this)}
                    />
                <input type="button" value="X" className="x-button" 
                    onClick={function(){
                    this.props.mainMode();
                    }.bind(this)}
                    />
            </div>
        );
    }
}

export default ProductButtons;