import React, {Component} from 'react';
import { Link } from "react-router-dom";

class Header extends Component{
    render(){
        var mainmenuList = [];
        var mainMenus = [
            {id: 1,name: "ABOUT",link:"/about"},
            {id: 2,name: "BRAND",link:"/nike"},
            {id: 3,name: "EVENT",link:"/event"}
          ];
        var i = 0;
        while(i<mainMenus.length){
            mainmenuList.push(<li key={mainMenus[i].id}><Link to ={mainMenus[i].link}>{mainMenus[i].name}</Link></li>)
            i++;
        }

        var rightmenuList = [];
        var rightMenus = [
            {id: 4,name: "Q/A",link:"/qna"},
            {id: 5,name: "LOGIN",link:"/login"}    
          ];
        i = 0;
        while(i<rightMenus.length){
            rightmenuList.push(<li key={rightMenus[i].id}><Link to={rightMenus[i].link}>{rightMenus[i].name}</Link></li>)
            i++;
        }

      return (  
        <header className="header-area">
            <div className="header-contents">
                <Link to="/"><img className="logo" src="img/logo/FLEX-LOGO-200-50_arrange.png" alt="logo"  width="160px" height="40px" /> </Link>
                <Link to="/"><img className="search-icon" alt="search-icon" src="img/icon/search_icon.png" width="32px" height="32px"/></Link>
                <nav>
                    <ul className="main-menu">
                        {mainmenuList}
                    </ul>   
                    <ul className="right-menu">
                        {rightmenuList}
                    </ul>
                </nav>
            </div>
        </header>
      );
    }
  }

export default Header;