import React, { Component } from 'react';

function numberFormat(inputNumber) {
    return inputNumber.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
}

class ProductSubinfo extends Component {
    render() {

        return (
            <div>
                <div className="price-info">
                    <div className="highest-bid">구매 최고가 : {numberFormat(this.props.product.highestBuyPrice)}원 사이즈 : 260</div>
                    <div className="lowest-ask">판매 최저가 : {numberFormat(this.props.product.lowestSellPrice)}원 사이즈 : 290</div>
                </div>
                <div className="product-sub-info">
                    
                    <div>상품 정보</div>
                {this.props.product.productSubinfo}
                
                </div>
            </div>
            );
    }
}
                    
export default ProductSubinfo;