import React, { Component } from 'react';

class ProductComment extends Component {
    render() {
        const left = {
            float: 'left'
          }
        return (
            <div className="product-comment">
            <div className="product-common-title" style={left}>
              <div className="product-common-title-icon" style={left} >
                <img src="img/icon/square_icon.png" width="27px" height="27px" alt="square_icon" />
              </div>
              후기
                              </div>
            <div className="product-comment-contents">
              <div className="rate-area" style={left}>
                <div className="rate-icon" style={left} >
                  <img src="img/icon/square_icon.png" width="75px" height="75px" alt="rate_icon" />
                  <img src="img/icon/square_icon.png" width="75px" height="75px" alt="rate_icon" />
                  <img src="img/icon/square_icon.png" width="75px" height="75px" alt="rate_icon" />
                  <img src="img/icon/square_icon.png" width="75px" height="75px" alt="rate_icon" />
                  <img src="img/icon/square_icon.png" width="75px" height="75px" alt="rate_icon" />
                </div>
                <div className="rate-txt">총 리뷰 : 37건</div>
              </div>
  
              <div className="comment-look-detail">
                자세히 보기
                                  </div>
              <div className="comment-title" id="size">
                사이즈
                                  </div>
              <div className="comment-detail" id="size">
                정사이즈에요 72%
                                  </div>
              <div className="comment-title" id="color">
                색상
                                  </div>
              <div className="comment-detail" id="color">
                사진보다 밝아요 70%
                                  </div>
              <div className="comment-title" id="comport">
                착화감
                                  </div>
              <div className="comment-detail" id="comport">
                발 볼이 좁아요 68%
                                  </div>
            </div>
          </div>
        );
    }
}

export default ProductComment;